    padding-top: 8rem !important;
    padding-bottom: 8rem !important;
    display: block;
    align-items: stretch; }
    .wp-block-borncreative-the-process-block .row .column1 {
      width: 100%; }
    .wp-block-borncreative-the-process-block .row .media.column1 img,
