/*
* For styles only loaded in the editor
* These styles are actually inlined using webpack style-loader.  They are not loaded using WordPress enqueuing.
*/
@media (min-width: 768px) {
  .wp-admin .col-full {
    max-width: 66.4989378333em;
    margin-left: auto;
    margin-right: auto;
    padding: 0 2.617924em;
    box-sizing: content-box; }
  .wp-admin .col-full:after,
  .wp-admin .col-full:before {
    content: "";
    display: table; }
  .wp-admin .col-full:after {
    content: " ";
    display: block;
    clear: both; } }

@media (max-width: 66.49894em) {
  .wp-admin .col-full {
    margin-left: 2.617924em;
    margin-right: 2.617924em;
    padding: 0; } }

.wp-admin .editor-styles-wrapper .storefront-has-sidebar .wp-block {
  max-width: 100%;
  overflow: hidden; }

.wp-admin .wp-block-borncreative-header-block .media:hover,
.wp-admin .wp-block-borncreative-header-block .media:focus {
  cursor: pointer; }
  .wp-admin .wp-block-borncreative-header-block .media:hover:after,
  .wp-admin .wp-block-borncreative-header-block .media:focus:after {
    position: absolute;
    content: '';
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    border: 4px solid lightgreen;
    pointer-events: none; }

.wp-admin .wp-block-borncreative-header-block .media {
  position: relative; }
  .wp-admin .wp-block-borncreative-header-block .media button {
    border: none;
    padding: 0;
    display: block;
    width: 100%;
    height: 100vh; }
